import React, { useState, useEffect } from "react";

//3 TanStack Libraries!!!
import {
  flexRender,
  getCoreRowModel,
  getSortedRowModel,
  useReactTable,
  getFilteredRowModel,
} from "@tanstack/react-table";
import { useInfiniteQuery } from "@tanstack/react-query";
import { useVirtual } from "react-virtual";
import styled from "styled-components";
import "./styles/Common.css";
import "./styles/Table.css";
import * as _ from "lodash";
const ARROW_IMG = "/static/rca/static/media/sort_arrows.1e4110c7.svg";
const ARROW_DOWN_IMG = "/static/rca/static/media/sort_arrows_down.c46705b8.svg";
const ARROW_UP_IMG = "/static/rca/static/media/sort_arrows_up.87992429.svg";
const FILTER_OFF_IMG = "/static/rca/static/media/filter_off.d0051c12.svg";
const FILTER_ON_IMG = "/static/rca/static/media/filter_on.34232411.svg";
const EMPTY_TABLE_MSG = "There are no rows that match your criteria";

/* eslint-disable react/prop-types */

const fetchSize = 25;

const Styles = styled.div`
  table {
    border-collapse: separate;
    border-spacing: 0;
    table-layout: fixed;
  }

  thead {
    margin: 0;
    position: sticky;
    top: 0;
    z-index: 1;
  }

  .thTransition {
    overflow: hidden;
    transition-property: height;
    transition-duration: 0.2s;
    transition-timing-function: ease-in;
    padding: 0.5rem;
  }

  .table-container {
    overflow: auto;
  }
  *::-webkit-scrollbar {
    // display: none;
    width: 5px;
    height: 5px;
  }

  *::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    // margin-top: 37px;
  }

  *::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
  }

  *::-webkit-scrollbar-corner {
    display: none;
  }


  th {
    padding: 0 !important;
  }

  th,
  td {
    margin: 0;
    padding: 0.5rem;
    position: relative;

    min-width: 5%;
    max-width: 100%;

    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

    .resizer {
      display: inline-block;
      width: 10px;
      height: 100%;
      position: absolute;
      right: 0;
      top: 0;
      transform: translateX(50%);
      z-index: 1;
      ${"" /* prevents from scrolling while dragging on touch devices */}
      touch-action:none;
      cursor: col-resize;
    }
  }
`;

function Table({
  title,
  data,
  quickActions,
  columnsOrder,
  emptyTableMsg,
  columnsSize,
  cellClassName,
  tableKey,
  height,
}) {
  //we need a reference to the scrolling element for logic down below
  const tableContainerRef = React.useRef();

  const [sorting, setSorting] = React.useState([]);

  const [postFilteringData, setPostFilteringData] = useState(data);

  const onQuickActionsClick = (row, index) => {
    if (index === quickActionsClickedRowIndex) {
      setQuickActionsClickedRowIndex(-1);
    } else {
      setQuickActionsClickedRowIndex(index);
    }
  };

  function onFilterClick(key) {
    if (filtersClicked.has(key)) {
      filtersClicked.set(key, !filtersClicked.get(key));
    } else {
      filtersClicked.set(key, true);
    }
    setFiltersClicked(new Map(filtersClicked));
  }

  const filterTypes = React.useMemo(() => ({
    text: (row, id, filterValue) => {
      const rowValue = row.getValue(id);
      return rowValue !== undefined
        ? String(rowValue)
            .toLowerCase()
            .includes(String(filterValue).toLowerCase())
        : true;
    },
  }));

  const calcColumns = () => {
    var result =
      data && data.length > 0
        ? Object.keys(data[0]).map((key) => {
            var res = {
              id: key,
              accessorKey: key,
              header: key,
              filterFn: "text",
            };
            if (columnsSize?.[key] !== undefined) {
              Object.assign(res, { width: columnsSize[key] });
            }
            return res;
          })
        : [];
    if (columnsOrder)
      result.sort(
        (a, b) => columnsOrder.indexOf(a.id) - columnsOrder.indexOf(b.id)
      );
    if (quickActions != null) {
      result = [
        {
          id: "quickActions",
          enableColumnFilter: false,
          enableResizing: false,
        },
      ].concat(result);
    }
    return result;
  };

  // const columns = React.useMemo(
  //   () => [
  //     {
  //       accessorKey: "id",
  //       header: "ID",
  //       size: 60,
  //     },
  //     {
  //       accessorKey: "firstName",
  //       cell: (info) => info.getValue(),
  //     },
  //     {
  //       accessorFn: (row) => row.lastName,
  //       id: "lastName",
  //       cell: (info) => info.getValue(),
  //       header: () => <span>Last Name</span>,
  //     },
  //     {
  //       accessorKey: "age",
  //       header: () => "Age",
  //       size: 50,
  //     },
  //     {
  //       accessorKey: "visits",
  //       header: () => <span>Visits</span>,
  //       size: 50,
  //     },
  //     {
  //       accessorKey: "status",
  //       header: "Status",
  //     },
  //     {
  //       accessorKey: "progress",
  //       header: "Profile Progress",
  //       size: 80,
  //     },
  //     {
  //       accessorKey: "createdAt",
  //       header: "Created At",
  //       cell: (info) => info.getValue().toLocaleString(),
  //     },
  //   ],
  //   []
  // );

  const [filtersClicked, setFiltersClicked] = useState(new Map());
  const [columns, setColumns] = useState(calcColumns());
  const [quickActionsClickedRowIndex, setQuickActionsClickedRowIndex] =
    useState(-1);

  const isQuickActionsAvailable = quickActions != null;

  useEffect(() => {
    const handleClick = (event) => {
      if (quickActionsClickedRowIndex === -1) return;
      var clickedInContext = false;
      const arr = document.getElementsByClassName("k2-context-menu");
      for (var i = 0; i < arr.length; i++) {
        if (arr[i].contains?.(event.target)) {
          clickedInContext = true;
          break;
        }
      }
      if (!clickedInContext) {
        setQuickActionsClickedRowIndex(-1);
      }
    };

    window.addEventListener("click", handleClick);

    return () => {
      window.removeEventListener("click", handleClick);
    };
  });

  useEffect(() => {
    setColumns(calcColumns());
  }, [data, columnsOrder]);

  const fetchData = (start, size, sorting) => {
    const dbData = [...postFilteringData];
    if (sorting.length) {
      const sort = sorting[0];
      const { id, desc } = sort;
      dbData.sort((a, b) => {
        if (desc) {
          return a[id] < b[id] ? 1 : -1;
        }
        return a[id] > b[id] ? 1 : -1;
      });
    }

    return {
      data: dbData.slice(start, start + size),
    };
  };

  const [columnFilters, setColumnFilters] = React.useState([]);

  //react-query has an useInfiniteQuery hook just for this situation!
  const {
    data: scrollableData,
    fetchNextPage,
    isFetching,
    isLoading,
  } = useInfiniteQuery(
    ["table-data-" + (tableKey ?? 0), sorting], //adding sorting state as key causes table to reset and fetch from new beginning upon sort
    async ({ pageParam = 0 }) => {
      const start = pageParam * fetchSize;
      const fetchedData = fetchData(start, fetchSize, sorting); //pretend api call
      return fetchedData;
    },
    {
      getNextPageParam: (_lastGroup, groups) => groups.length,
      keepPreviousData: false,
      refetchOnWindowFocus: false,
    }
  );

  //we must flatten the array of arrays from the useInfiniteQuery hook
  const flatData = scrollableData?.pages?.flatMap((page) => page.data) ?? [];
  const totalDBRowCount = postFilteringData?.length ?? 0;
  const totalFetched = flatData.length;

  //called on scroll and possibly on mount to fetch more data as the user scrolls and reaches bottom of table
  const fetchMoreOnBottomReached = React.useCallback(
    (containerRefElement) => {
      if (containerRefElement) {
        const { scrollHeight, scrollTop, clientHeight } = containerRefElement;
        //once the user has scrolled within 300px of the bottom of the table, fetch more data if there is any
        if (
          scrollHeight - scrollTop - clientHeight < 300 &&
          !isFetching &&
          totalFetched < totalDBRowCount
        ) {
          fetchNextPage();
        }
      }
    },
    [fetchNextPage, isFetching, totalFetched, totalDBRowCount]
  );

  //a check on mount and after a fetch to see if the table is already scrolled to the bottom and immediately needs to fetch more data
  React.useEffect(() => {
    fetchMoreOnBottomReached(tableContainerRef.current);
  }, [fetchMoreOnBottomReached]);

  useEffect(() => {
    const postFilterData = data?.filter(
      (row) =>
        !columnFilters?.some((entry) => {
          return !String(row[entry.id])
            .toLowerCase()
            .includes(String(entry.value).toLowerCase());
        })
    );
    setPostFilteringData(postFilterData);
  }, [columnFilters]);

  const table = useReactTable({
    data: flatData,
    columns,
    columnResizeMode: "onChange",
    enableColumnResizing: true,
    state: {
      sorting,
      columnFilters,
    },
    defaultColumn: {
      size: 250,
    },
    filterFns: filterTypes,
    onSortingChange: setSorting,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    onColumnFiltersChange: setColumnFilters,
  });

  const { rows } = table.getRowModel();
  //Virtualizing is optional, but might be necessary if we are going to potentially have hundreds or thousands of rows
  const rowVirtualizer = useVirtual({
    parentRef: tableContainerRef,
    size: rows.length,
    overscan: 10,
  });
  const { virtualItems: virtualRows, totalSize } = rowVirtualizer;
  const paddingTop = virtualRows.length > 0 ? virtualRows?.[0]?.start || 0 : 0;
  const paddingBottom =
    virtualRows.length > 0
      ? totalSize - (virtualRows?.[virtualRows.length - 1]?.end || 0)
      : 0;

  if (isLoading) {
    return <>Loading...</>;
  }
  return (
    <Styles>
      {title?.length > 0 && (
        <div style={{ marginBottom: "20px" }}>
          <h2 style={{ marginBottom: "20px" }}>{title}</h2>
          <hr />
        </div>
      )}
      {data?.length ? (
        <>
          <div
            className="table-container"
            onScroll={(e) => fetchMoreOnBottomReached(e.target)}
            ref={tableContainerRef}
            style={{ maxHeight: height ?? "200px" }}
          >
            <table className="rounded-corners">
              <thead>
                {table
                  .getHeaderGroups()
                  .map((headerGroup, headerGroupIndex) => (
                    <tr key={"thead.tr." + headerGroupIndex}>
                      {headerGroup.headers.map((header, headerIndex) => {
                        const columnFilterValue =
                          header.column.getFilterValue();
                        return (
                          <th
                            key={"thead.tr.th." + headerIndex}
                            colSpan={header.colSpan}
                            style={{
                              width:
                                header.id === "quickActions"
                                  ? "40px"
                                  : header.getSize(),
                              maxWidth:
                                header.id === "quickActions" ? "40px" : "",
                            }}
                          >
                            <div
                              className="thTransition"
                              style={{
                                height:
                                  filtersClicked.has(header.id) &&
                                  filtersClicked.get(header.id)
                                    ? "55px"
                                    : "37px",
                              }}
                            >
                              <span className="header-cell">
                                {flexRender(
                                  header.column.columnDef.header,
                                  header.getContext()
                                )}
                                <span className="icons-wrapper">
                                  {header.column.getCanFilter() && (
                                    <img
                                      alt={
                                        filtersClicked.has(header.id) &&
                                        filtersClicked.get(header.id)
                                          ? "Filter on"
                                          : "Filter off"
                                      }
                                      className="icons"
                                      src={
                                        filtersClicked.has(header.id) &&
                                        filtersClicked.get(header.id)
                                          ? FILTER_ON_IMG
                                          : FILTER_OFF_IMG
                                      }
                                      style={{ cursor: "pointer" }}
                                      onClick={() => onFilterClick(header.id)}
                                    />
                                  )}
                                  {header.column.getCanSort() && (
                                    <span>
                                      <img
                                        alt={
                                          header.isSorted
                                            ? header.isSortedDesc
                                              ? "Arrow Down"
                                              : "Arrow Up"
                                            : "Arrow"
                                        }
                                        src={
                                          header.column.getIsSorted()
                                            ? header.column.getIsSorted() ===
                                              "desc"
                                              ? ARROW_DOWN_IMG
                                              : ARROW_UP_IMG
                                            : ARROW_IMG
                                        }
                                        className="icons"
                                        onClick={() =>
                                          header.column.toggleSorting()
                                        }
                                      />
                                    </span>
                                  )}
                                </span>
                              </span>
                              {filtersClicked.has(header.id) &&
                                filtersClicked.get(header.id) && (
                                  <div>
                                    {header.column.getCanFilter() ? (
                                      <input
                                        type="text"
                                        value={columnFilterValue ?? ""}
                                        onChange={(e) =>
                                          header.column.setFilterValue(
                                            e.target.value
                                          )
                                        }
                                        placeholder={`Search... (${totalDBRowCount})`}
                                        className="columnFilter"
                                        style={{
                                          width: "100%",
                                          maxWidth: "152px",
                                          padding: "1px",
                                        }}
                                      />
                                    ) : null}
                                  </div>
                                )}
                              {header.column.getCanResize() && (
                                <div
                                  {...{
                                    onMouseDown: header.getResizeHandler(),
                                    onTouchStart: header.getResizeHandler(),
                                    className: `resizer ${
                                      header.column.getIsResizing()
                                        ? "isResizing"
                                        : ""
                                    }`,
                                  }}
                                />
                              )}
                            </div>
                          </th>
                        );
                      })}
                    </tr>
                  ))}
              </thead>
              <tbody>
                {paddingTop > 0 && (
                  <tr>
                    <td style={{ height: `${paddingTop}px` }} />
                  </tr>
                )}

                {virtualRows.map((virtualRow) => {
                  const row = rows[virtualRow.index];
                  const rowIndex = virtualRow.index;
                  const rowQuickActions = quickActions?.(getRowAsObject(row));
                  return (
                    <tr key={"tbody.tr." + rowIndex}>
                      {row.getVisibleCells().map((cell, cellIndex) => {
                        return (
                          <td
                            key={"tbody.tr." + rowIndex + ".td." + cellIndex}
                            className={
                              cellClassName?.(
                                getRowAsObject(row),
                                cell.column.id
                              ) ?? ""
                            }
                            style={{
                              overflow:
                                isQuickActionsAvailable && cellIndex === 0
                                  ? "visible"
                                  : "hidden",
                            }}
                            title={cell.getValue()}
                          >
                            {isQuickActionsAvailable && cellIndex === 0 ? (
                              <>
                                <div className="reactTableCell">
                                  <span>
                                    {rowQuickActions?.length > 0 && (
                                      <div
                                        className="k2-context-btn"
                                        onClick={(e) => {
                                          e.stopPropagation();
                                          onQuickActionsClick(row, rowIndex);
                                        }}
                                      />
                                    )}
                                  </span>
                                </div>
                                <span
                                  style={{
                                    display:
                                      quickActionsClickedRowIndex === rowIndex
                                        ? "inline-block"
                                        : "none",
                                    left: "30px",
                                    position: "absolute",
                                    top: "30px",
                                    zIndex: "10",
                                    background: "white",
                                  }}
                                >
                                  <ul className="k2-context-menu">
                                    {rowQuickActions.map(
                                      (action, actionIndex) => (
                                        <li
                                          key={
                                            "tbody.tr." +
                                            rowIndex +
                                            ".quickActions." +
                                            actionIndex
                                          }
                                          onClick={() => {
                                            rowQuickActions[
                                              actionIndex
                                            ].onClick(getRowAsObject(row));
                                          }}
                                        >
                                          {action.title}
                                        </li>
                                      )
                                    )}
                                  </ul>
                                </span>
                              </>
                            ) : (
                              flexRender(
                                cell.row.original[cell.column.id],
                                cell.getContext()
                              )
                            )}
                          </td>
                        );
                      })}
                    </tr>
                  );
                })}
                {paddingBottom > 0 && (
                  <tr>
                    <td style={{ height: `${paddingBottom}px` }} />
                  </tr>
                )}
              </tbody>
            </table>
          </div>
          <div style={{ flex: 1, marginTop: "10px" }}>
            Fetched {Math.min(flatData.length, totalDBRowCount)} of{" "}
            {totalDBRowCount} Rows.
          </div>
        </>
      ) : (
        <div>
          <span>{emptyTableMsg ?? EMPTY_TABLE_MSG}</span>
        </div>
      )}
    </Styles>
  );
}

const getRowAsObject = (row) => {
  return row.original;
};

export default Table;
