import React, { useEffect, createContext, useState } from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import {
  QueryClient,
  QueryClientProvider,
} from "@tanstack/react-query";
import { Publish } from "./components/containers/Publish";
import AdditionalMessageInfo from "./components/containers/AdditionalMessageInfo";
import MessageData from "./components/containers/MessageData";
import CategoryContainer from "./components/display/CategoryContainer";
import * as _ from "lodash";

export const ROUTES_NAMES = {
  Publish: {name: "GG Simulator"},
  MessageData: { name: "MessageData" },
  AdditionalMessageInfo: { name: "AdditionalMessageInfo" },
};

export const SEARCH_PARAMS_TO_LABELS = {
  lut: "LU Type",
  iid: "IID",
  bid: "BID",
}

//Added const label array for GG section --- Yonatan\\
export const SEARCH_PARAMS_TO_LABELS_GG = {
  tbl_type: "Table Type",
  lkp_topic: "Lookup topic name",
}



export const APP_NAME = "GG Simulator";

export const GG_Simulator_LIB_LU_NAME = "GG Simulator";

export const DataContext = createContext();

function App() {
  const [luTypes, setLuTypes] = useState();
  console.log("~~~DEBUG setNavigationMenu stage~~~");
  useEffect(() => {
    window.k2api.setNavigationMenu([
      {name: "Publish",path: "/Publish"},
    ]);
    const invokeFabricCommand = async () =>
      window.k2api.invokeFabricCommands("list lut");
    invokeFabricCommand().then((response) => {
      const results = response.results[0];
      console.log("~~~DEBUG results stage = ~~~" + results.resultSet?.rows);
      setLuTypes(
        results.resultSet?.rows
          ?.filter((row) => !_.isEqual(row[0], GG_Simulator_LIB_LU_NAME))
          ?.map((row) => row[0])
      );
    });
  }, []);

  const queryClient = new QueryClient();

  return (
    <DataContext.Provider value={{ luTypes }}>
      <QueryClientProvider client={queryClient}>
      <Router>
        <div className="main-container">
          <Routes>
            <Route
              path="/Publish"
              element={<CategoryContainer Component={<Publish />} />}
            />
            <Route
              path="/*"
              element={<Navigate replace to="/Publish" />}
            />
          </Routes>
        </div>
      </Router>
      </QueryClientProvider>
    </DataContext.Provider>
  );
}

export default App;
