import React from "react";
import Breadcrumb from "./Breadcrumb";
import ThemeSwitcher from "./ThemeSwitcher";
/* eslint-disable react/prop-types */
function CategoryContainer({ Component }) {
  return (
    <>
      <Breadcrumb />
      <ThemeSwitcher />
      {Component}
    </>
  );
}
export default CategoryContainer;
