import React from "react";

// eslint-disable-next-line react/prop-types
const Popup = ({ isOpen, title, text, setIsOpen }) => {
  return (
    <div
      className={
        isOpen ? "k2-popup-wrapper k2-wrapper-visible" : "k2-popup-wrapper"
      }
      onClick={() => setIsOpen(false)}
    >
      <div className="k2-card k2-popup-content">
        <span className="k2-popup-title">{title}</span>
        <hr className="k2-popup-seperator" />
        {text}
        <div className="k2-buttons-container">
          <div className="k2-card-ok-cancel-btn">OK</div>
        </div>
      </div>
    </div>
  );
};

export default Popup;
