import React, { useState, useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import { SEARCH_PARAMS_TO_LABELS } from "../../App";

/* eslint-disable react/prop-types */
function Header(props) {
  const [isAutoRefresh, setIsAutoRefresh] = useState(false);
  const [autoRefreshIntervalId, setAutoRefreshIntervalId] = useState();
  const [isBodyCollapsed, setIsBodyCollapsed] = useState(true);
  const [searchParams, setSearchParams] = useSearchParams();
  const onAutoRefreshChange = (event) => {
    const value = event.target.checked;
    setIsAutoRefresh(value);
    if (!value) {
      clearInterval(autoRefreshIntervalId);
      setAutoRefreshIntervalId();
    } else {
      setAutoRefreshIntervalId(setInterval(() => props.onRefresh?.(), 3000));
    }
  };

  useEffect(() => {
    return function cleanup() {
      //  clear timeout when the component unmounts
      clearInterval(autoRefreshIntervalId);
    };
  });

  useEffect(() => {
    if (!searchParams.keys().next().done) setIsBodyCollapsed(true);
    else setIsBodyCollapsed(false)
  }, [searchParams]);

  const getSearchParamsTitle = () => {
    var result = "";
    const searchParamsKeys = searchParams.keys();
    var key = searchParamsKeys.next();
    while (!key.done) {
      result +=
        SEARCH_PARAMS_TO_LABELS[key.value] + ": " + searchParams.get(key.value);
      key = searchParamsKeys.next();
      if (!key.done) result += ", ";
    }
    return result;
  };

  return (
    <div className="k2-card">
      <div
        className="k2-header"
        onClick={() => setIsBodyCollapsed(!isBodyCollapsed)}
      >
        <span style={{ display: "flex", alignItems: "center" }}>
          <span className="k2-card-title">{props.title}</span>
          {!searchParams.entries().next().done && (
            <>
              <span style={{ marginLeft: "10px", borderLeft: "1px solid rgba(0, 0, 0, 0.2)", borderRight: "1px solid rgba(255, 255, 255, 0.5)", height: "20px"}}></span>
              <span style={{ marginLeft: "10px" }}>
                {getSearchParamsTitle()}
              </span>
            </>
          )}
        </span>
        <div className="k2-header-actions">
          {props.children}
          {props.allowRefresh && (
            <>
              <div className="k2-header-field">
                <button
                  className="k2-secondary-btn"
                  onClick={(e) => {
                    e.stopPropagation();
                    props.onRefresh?.();
                  }}
                >
                  Refresh <b></b>
                </button>
              </div>
              <div className="k2-header-field">
                <div
                  className="k2-auto-refresh"
                  onClick={(e) => {
                    e.stopPropagation();
                  }}
                >
                  Auto Refresh
                  <input
                    name="Auto Refresh"
                    type="checkbox"
                    style={{
                      marginRight: "10px",
                      marginLeft: "15px",
                      marginTop: "0px",
                    }}
                    onChange={onAutoRefreshChange}
                    value={isAutoRefresh}
                  />
                </div>
              </div>
            </>
          )}
          {props.body && (
            <div
              className="k2-header-field"
              style={{ marginRight: "5px", rotate: "90deg" }}
            >
              <h1>
                <span className={"header-collapse" + (isBodyCollapsed ? " collapsed" : "")} />
              </h1>
            </div>
          )}
        </div>
      </div>
      {!!props.body && (
        <div className={isBodyCollapsed ? "k2-body closed" : "k2-body"}>
          <hr />
          {props.body}
        </div>
      )}
    </div>
  );
}
export default Header;
