import React, { useEffect, useState, useContext } from "react";
import { useSearchParams } from "react-router-dom";
import Table from "../display/ScrollTable";
import Header from "../display/CategoryHeader";
import _ from "lodash";
import WithRouter from "../WithRouter";
import { DataContext } from "../../App";
import Popup from "../display/Popup";
import { SEARCH_PARAMS_TO_LABELS_GG } from "../../App";
import Skeleton from "@mui/material/Skeleton";

//--- Part of code made by Yonatan ---\\

function LUIComp() {
  const [searchParams, setSearchParams] = useSearchParams();
  const [selectedLUT, setSelectedLUT] = useState(searchParams.get("lut") ?? "");
  const [failureMsg, setFailureMsg] = useState();
  const [successMsg, setSuccessMsg] = useState();
  const [isFailed, setIsFailed] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [targetOption, setTargetOption] = useState("LU Table");
  const [inputValue1, setInputValue1] = useState();
  const [commandOption, setCommandOption] = useState("DML command");
  const [specificCommand, setSpecificCommand] = useState();
  const [sourceDataButton, setSourceDataButton] = useState("");
  const [statementList, setStatementList] = useState("Insert");
  const [interfaceList, setInterfaceList] = useState([]);
  const [interfaceOption, setInterfaceOption] = useState();
  const [topicName, setTopicName] = useState();
  const [opTimeStamp, setOPTimeStamp] = useState();
  const [inputPos, setPOS] = useState();
  const [beforeValues, setBeforeValues] = useState("false");
  const [replica, setReplica] = useState("false");
  const [debug, setDebug] = useState("false");
  const [customLUTableName, setCustomLUTableName] = useState();
  const [flowTime, setFlowTimes] = useState(1);

  const { luTypes } = useContext(DataContext);

  useEffect(() => {
    const lut = searchParams.get("lut") ?? "";
    setSelectedLUT(lut);
  }, [searchParams]);

  // will run once every load page
  useEffect(() => {
    const command = "list interfaces";
    fetchData(command);
    console.log("interfaceList ~~~`");
  }, []);

  function handleFailure(reason) {
    console.error(reason);
    setIsFailed(true);
    const failure = (
      <>
        <p>Failure occured while sending messages to Kafka topic.</p>
        <p>{"Error message: "}</p>
        <p>{reason}</p>
      </>
    );
    setFailureMsg(failure);
    setIsLoading(false);
    setIsPopupOpen(true);
    searchParams.delete("lut");
    searchParams.delete("iid");
    setSearchParams(searchParams);
  }

  const onFormSubmit = (event) => {
    event.preventDefault();
    event.persist();
    console.log("submit button clicked");
    let table_type = "LU Table";

    //handle empty case
    const currentTimestamp = new Date().getTime();
    let opTS = opTimeStamp;
    if (!opTimeStamp) {
      opTS = currentTimestamp;
    }

    let broadwayflow = "bwGGSimulator";

    //DML command is chosen execute - bwGenerateDMLStmts
    if (commandOption == "From source") {
      broadwayflow = "bwGenerateDMLStmts";
    }

    //default values
    let pos =
      inputPos !== null && inputPos !== undefined
        ? inputPos
        : "00000000020030806864";
    let topic =
      topicName !== null && topicName !== undefined ? topicName : null;
    let parKey =
      inputValue1 !== null && inputValue1 !== undefined ? inputValue1 : null;
    let sqlStmt =
      specificCommand !== null && specificCommand !== undefined
        ? specificCommand
        : null;
    let interFace =
      interfaceOption !== null && interfaceOption !== undefined
        ? interfaceOption
        : null;
    let sourceData =
      sourceDataButton !== null && sourceDataButton !== undefined
        ? sourceDataButton
        : null;
    let customLUTableName1 = customLUTableName || null;

    if (targetOption == "Reference Table") {
      table_type = "Reference";
    }
    if (targetOption == "Lookup Table") {
      table_type = "Lookup";
    }

    handleSubmit(
      broadwayflow,
      selectedLUT,
      parKey,
      sqlStmt,
      statementList,
      sourceData,
      interFace,
      topic,
      beforeValues,
      replica,
      opTS,
      pos,
      table_type,
      customLUTableName1
    );
  };

  //start broadway flow
  const handleSubmit = async (
    broadwayflow,
    selectedLUT,
    inputValue1,
    sqlStmt,
    statementList,
    sourceData,
    interFace,
    topicName,
    beforeValues,
    replica,
    opTS,
    pos,
    table_type,
    customLUTableName1
  ) => {
    setIsLoading(true);
    // const sleep = (ms) => new Promise((r) => setTimeout(r, ms));
    // await sleep(5000);

    console.log("~~~~ handleSubmit ~~~~");
    console.log("flow = " + broadwayflow);
    console.log("statementList = " + statementList);

    let command = [];
    if (broadwayflow == "bwGenerateDMLStmts") {
      command = [
        "broadway GG_Simulator." +
          broadwayflow +
          " 'lu_name'='" +
          selectedLUT +
          "', topic_name='" +
          topicName +
          "', op_ts='" +
          opTS +
          "', pos='" +
          pos +
          "', no_before='" +
          beforeValues +
          "', replicate='" +
          replica +
          "', partition_key='" +
          inputValue1 +
          "', replicate='" +
          replica +
          "', select_stmt=\"" +
          sourceData +
          "\", interface='" +
          interFace +
          "', operation='" +
          statementList +
          "', table_type='" +
          table_type +
          "', 'debug'=" +
          debug +
          " custom_lu_table_name='" +
          customLUTableName1 +
          "'",
      ];
    } else {
      command = [
        "broadway GG_Simulator." +
          broadwayflow +
          " 'lu_name'='" +
          selectedLUT +
          "', sql_stmt=\"" +
          sqlStmt +
          "\", topic_name='" +
          topicName +
          "', op_ts='" +
          opTS +
          "', pos='" +
          pos +
          "', no_before='" +
          beforeValues +
          "', replicate='" +
          replica +
          "', partition_key='" +
          inputValue1 +
          "', replicate='" +
          replica +
          "', table_type='" +
          table_type +
          "', 'debug'=" +
          debug +
          " custom_lu_table_name='" +
          customLUTableName1 +
          "'",
      ];
    }

    const invokeFabricCommand = async () =>
      window.k2api.invokeFabricCommands(command);
    invokeFabricCommand()
      .then(
        (response) => {
          const resultsLength = response.results?.length;
          const rows = response.results[0]?.resultSet?.rows;
          const isFailure =
            resultsLength !== command.length ||
            !_.isEmpty(response.results[resultsLength - 1].error) ||
            _.isEmpty(rows?.[0]?.[0]);
          if (!isFailure) {
            console.log("broadway is running");
            setIsPopupOpen(true);
            setIsFailed(false);
            setFailureMsg();
            let message = "";
            message = message.concat(
              rows[0][0].PUBLISHED,
              " records were published successfully"
            );
            if (rows[0][0].TOPIC && rows[0][0].TOPIC !== "null") {
              message = message.concat(" to topic ", rows[0][0].TOPIC);
            }
            message = message.concat(
              ". ",
              rows[0][0].SKIPPED,
              " records were skipped."
            );
            setSuccessMsg(message);
          } else {
            setSuccessMsg();
            handleFailure(
              response.results[resultsLength - 1].error?.message ??
                "Unexpected error occured"
            );
          }
          setIsLoading(false);
        },
        (reason) => {
          handleFailure(reason);
        }
      )
      .catch((reason) => {
        handleFailure(reason);
      });
  };

  //fetch data
  async function fetchData(commands) {
    try {
      const invokeFabricCommand = async () =>
        window.k2api.invokeFabricCommands(commands);
      invokeFabricCommand().then((response) => {
        const rows = response.results[0]?.resultSet?.rows || [];
        if (rows.length > 0) {
          // Extract the data
          const data = rows.map((row) =>
            Array.isArray(row) && row.length > 0 ? row[0] : ""
          );
          setInterfaceList(data);
        }
      });
    } catch (error) {
      console.log("~~fetchData function error~~ : " + error);
    }
  }

  //Handel Target user chosen
  const handleTargetOption = (event) => {
    setTargetOption(event.target.value);
  };

  //Added const label array for Target section --- Yonatan\\
  const TARGET_SECTION = [
    { value: "LU Table", label: "LU Table" },
    { value: "Reference Table", label: "Reference Table" },
    { value: "Lookup Table", label: "Lookup Table" },
  ];
  //Added const label array for Message Data section --- Yonatan\\
  const MESSAGE_DATA = [
    { value: "DML command", label: "DML command" },
    { value: "From source", label: "From source" },
  ];

  const STATEMENT_LIST = [
    { value: "Insert", label: "Insert" },
    { value: "Update", label: "Update" },
    { value: "Delete", label: "Delete" },
  ];

  const INTERFACE_LIST = [
    { value: "CRM_DB", label: "CRM_DB" },
    { value: "ORDER_DB", label: "ORDER_DB" },
    { value: "BILLING_DB", label: "BILLING_DB" },
  ];

  const defaultList = [
    { value: "false", label: "false" },
    { value: "true", label: "true" },
  ];

  const defaultTopicName = [
    { value: "false", label: "false" },
    { value: "true", label: "true" },
  ];

  //Target section
  const handleRefTable = (event) => {
    setInputValue1(event.target.value);
    console.log("referenceTable = " + inputValue1);
  };

  const customLUTableNameOnChange = (event) => {
    setCustomLUTableName(event.target.value);
    console.log("customLUTableName = " + inputValue1);
  };

  //Message data section - handle command dropdown choose
  const handleCommandOption = (event) => {
    setCommandOption(event.target.value);
  };

  //case - specific command
  const handleSpecificCommand = (event) => {
    setSpecificCommand(event.target.value);
  };

  const handleSourceDataButton = (event) => {
    setSourceDataButton(event.target.value);
  };

  const handleStatementList = (event) => {
    setStatementList(event.target.value);
  };

  const handleInterface = (event) => {
    setInterfaceOption(event.target.value);
  };

  const handleTopicOption = (event) => {
    setTopicName(event.target.value);
  };

  const handleTimeStamp = (event) => {
    setOPTimeStamp(event.target.value);
  };

  const handlePOS = (event) => {
    setPOS(event.target.value);
  };

  const handleBeforeValues = (event) => {
    setBeforeValues(event.target.value);
  };

  const handleReplica = (event) => {
    setReplica(event.target.value);
  };

  const handleDebug = (event) => {
    setDebug(event.target.value);
  };

  const handleflowTimes = (event) => {
    setFlowTimes(event.value.target);
  };

  const topicNamePlaceholders = {
    "LU Table": "Default : IidFinder.schemaName.TableName",
    "Lookup Table": "Default : LKUP.schemaName.tableName_LKUP_partitionKey",
    "Reference Table": "Default : REF.schemaName.tableName",
  };

  return (
    <>
      <div className="lui-container k2-category-container3">
        <form onSubmit={onFormSubmit}>
          <div className="k2-card">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
              }}
            >
              <div>
                <h1>GG Simulator</h1>
              </div>
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "flex-end",
                }}
              >
                <label style={{ marginLeft: "5px" }}>Execute </label>
                <input
                  style={{ marginLeft: "5px", width: "25px" }}
                  type="text"
                  value={flowTime}
                  onChange={handleflowTimes}
                />
                <label style={{ marginLeft: "5px" }}>times</label>
                {isLoading ? (
                  <button disabled style={{ marginLeft: "25px" }}>
                    <div className="k2-btn-loader"></div>
                    Processing
                  </button>
                ) : (
                  <button style={{ marginLeft: "25px" }} type="submit">
                    Send message
                  </button>
                )}
              </div>
            </div>
          </div>

          {
            <>
              <div style={{ marginTop: "20px" }}>
                <Header>
                  <div
                    className="icon-header-container"
                    style={{ marginBottom: "15px" }}
                  >
                    <div
                      className="iconWrapper-card "
                      style={{ background: "rgb(146, 61, 240)" }}
                    >
                      <img
                        src={"/static/GG_SIMULATOR/static/media/luiSize.png"}
                      />
                    </div>
                    <h1>Target</h1>
                  </div>

                  <label className="formLabel">Select Target :</label>
                  <select value={targetOption} onChange={handleTargetOption}>
                    {TARGET_SECTION.map((option) => (
                      <option key={option.value} value={option.value}>
                        {option.label}
                      </option>
                    ))}
                  </select>
                  {targetOption === "LU Table" && ( // Display text box for LU type
                    <span style={{ marginBottom: "15px", marginLeft: "30px" }}>
                      <>
                        <label className="formLabel">LU Type* :</label>
                        <select
                          title={SEARCH_PARAMS_TO_LABELS_GG.lut}
                          required
                          name="lut"
                          value={selectedLUT}
                          onChange={(e) => setSelectedLUT(e.target.value)}
                        >
                          <option value="" disabled></option>
                          {luTypes?.map((lut) => (
                            <option value={lut} key={lut}>
                              {lut}
                            </option>
                          ))}
                        </select>
                      </>
                    </span>
                  )}

                  {targetOption === "Lookup Table" && ( // Display text box for Reference Table
                    <span style={{ marginBottom: "15px", marginLeft: "30px" }}>
                      <label className="formLabel">Partition key* :</label>
                      <input
                        type="text"
                        value={inputValue1}
                        onChange={handleRefTable}
                        required
                      />
                    </span>
                  )}

                  <>
                    <label
                      className="formLabel"
                      style={{ marginLeft: "30px", marginRight: "10px" }}
                    >
                      Custom Table Name :
                    </label>
                    <input
                      type="text"
                      value={customLUTableName}
                      onChange={customLUTableNameOnChange}
                    />
                  </>
                </Header>
              </div>

              <div style={{ marginTop: "20px" }}>
                <Header>
                  <div className="icon-header-container">
                    <div
                      className="iconWrapper-card "
                      style={{ background: "#f0832b" }}
                    >
                      <img
                        src={
                          "/static/GG_SIMULATOR/static/media/summery-per-icon.ac8ec8a3.svg"
                        }
                      />
                    </div>
                    <h1>Message data</h1>
                  </div>
                  <div></div>
                  <div style={{ marginTop: "20px" }}>
                    <label className="formLabel">Source data :</label>
                    <select
                      value={commandOption}
                      onChange={handleCommandOption}
                    >
                      {MESSAGE_DATA.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                    {commandOption === "DML command" && ( // Specific command text box
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          marginTop: "20px",
                        }}
                      >
                        <label className="formLabel">Enter command* :</label>
                        <textarea
                          id="dmlCommandTextArea"
                          style={{ width: "400px" }}
                          value={specificCommand}
                          onChange={handleSpecificCommand}
                          required
                        />
                      </div>
                    )}
                    {commandOption === "From source" && ( // stage - Source data
                      <>
                        <div
                          style={{
                            marginTop: "20px",
                            display: "flex",
                            alignItems: "center",
                          }}
                        >
                          <div>
                            <label className="formLabel">Operation* :</label>
                          </div>
                          <select
                            value={statementList}
                            onChange={handleStatementList}
                            required
                          >
                            {STATEMENT_LIST.map((option) => (
                              <option key={option.value} value={option.value}>
                                {option.label}
                              </option>
                            ))}
                          </select>
                          <div style={{ marginLeft: "30px" }}>
                            <label className="formLabel">Interface* :</label>
                          </div>
                          <select
                            value={interfaceOption}
                            onChange={handleInterface}
                            required
                          >
                            <option value=""></option>
                            {interfaceList.map((name, index) => (
                              <option key={index} value={name}>
                                {name}
                              </option>
                            ))}
                          </select>
                        </div>
                        <div
                          style={{
                            marginTop: "20px",
                            display: "flex",
                            alignItems: "center",
                          }}
                        >
                          <label className="formLabel">
                            Enter statement* :
                          </label>
                          <textarea
                            style={{ width: "400px" }}
                            value={sourceDataButton}
                            onChange={handleSourceDataButton}
                            required
                          />
                        </div>
                      </>
                    )}
                  </div>
                </Header>
              </div>

              <div style={{ marginTop: "20px" }}>
                <Header>
                  <div className="icon-header-container">
                    <div
                      className="iconWrapper-card "
                      style={{ background: "rgb(20,131,243)" }}
                    >
                      <img
                        src={
                          "/static/GG_SIMULATOR/static/media/generalData.041ee7ce.svg"
                        }
                      />
                    </div>
                    <h1>Additional message info</h1>
                  </div>

                  <div className="input-field">
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                      }}
                    >
                      <label className="formLabel">Topic name </label>
                      <input
                        style={{ width: "400px" }}
                        type="text"
                        value={topicName}
                        placeholder={topicNamePlaceholders[targetOption]}
                        onChange={handleTopicOption}
                      />
                    </div>
                  </div>

                  <div
                    className="input-field"
                    style={{
                      marginTop: "20px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <label className="formLabel">NO_before</label>
                    <select
                      style={{ width: "400px" }}
                      value={beforeValues}
                      onChange={handleBeforeValues}
                    >
                      {defaultList.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div
                    className="input-field"
                    style={{
                      marginTop: "20px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <label className="formLabel">Replicate</label>
                    <select
                      style={{ width: "400px" }}
                      value={replica}
                      onChange={handleReplica}
                    >
                      {defaultList.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div
                    className="input-field"
                    style={{
                      marginTop: "20px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <label className="formLabel">Op_ts</label>
                    <input
                      style={{ width: "400px" }}
                      type="text"
                      value={opTimeStamp}
                      onChange={handleTimeStamp}
                      placeholder="Default : Current ts"
                    />
                  </div>

                  <div
                    className="input-field"
                    style={{
                      marginTop: "20px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <label className="formLabel">Pos</label>
                    <input
                      style={{ width: "400px" }}
                      type="text"
                      value={inputPos}
                      onChange={handlePOS}
                      placeholder="Default : 00000000020030806864"
                    />
                  </div>

                  <div
                    className="input-field"
                    style={{
                      marginTop: "20px",
                      display: "flex",
                      alignItems: "center",
                    }}
                  >
                    <label className="formLabel">Debug</label>
                    <select
                      style={{ width: "400px" }}
                      value={debug}
                      onChange={handleDebug}
                    >
                      {defaultList.map((option) => (
                        <option key={option.value} value={option.value}>
                          {option.label}
                        </option>
                      ))}
                    </select>
                  </div>
                </Header>
              </div>
            </>
          }
        </form>
      </div>

      <Popup
        isOpen={isPopupOpen}
        title={isFailed ? "Failed" : "Success"}
        text={isFailed ? failureMsg : successMsg}
        setIsOpen={(val) => setIsPopupOpen(val)}
      />
    </>
  );
}

export const Publish = WithRouter(LUIComp);
