import React from "react";
import { ROUTES_NAMES, APP_NAME } from "../../App";
import { Link, useSearchParams } from "react-router-dom";
function Breadcrumb() {
  const [searchParams] = useSearchParams();
  const subPathArr = window.location.pathname
    .split("/")
    .filter((subPath) => subPath?.length > 0);
  return (
    <div className="breadcrumb-custom">
      <ul className="k2-breadcrumb">
        <li key="bc_0">
          <Link to="/">{APP_NAME}</Link>
        </li>
        <li key={"bc_1"}>
          <Link to={"/" + subPathArr[0]}>
            {ROUTES_NAMES[subPathArr[0]].name}
          </Link>
        </li>
        {subPathArr.length === 2 && (
          <li key={"bc_2"}>
            <Link to={"/" + subPathArr[0] + "/" + subPathArr[1]}>
              {ROUTES_NAMES[subPathArr[0]][subPathArr[1]]}
            </Link>
          </li>
        )}
        {!searchParams.entries().next().done && (
          <li key={"bc_3"}>
            <span>Results</span>
          </li>
        )}
      </ul>
    </div>
  );
}

export default Breadcrumb;
