import React, { useEffect, useState } from "react";
import { DarkModeSwitch } from "react-toggle-dark-mode";

export const Themes = {
  DARK: "Dark",
  LIGHT: "Light",
};

export const DEFAULT_THEME = Themes.LIGHT;

const ThemeSwitcher = () => {
  const [theme, setTheme] = useState(
    window.localStorage.getItem("theme") ?? DEFAULT_THEME
  );
  const switchToTheme = theme === Themes.DARK ? Themes.LIGHT : Themes.DARK;

  useEffect(() => {
    document.body.className =
      (window.localStorage.getItem("theme") ?? DEFAULT_THEME) === Themes.DARK
        ? "dark"
        : "";
  }, []);

  return (
    <div style={{ float: "right", margin: "10px 25px" }} title="Switch Theme">
      {/* <button
          className="k2-secondary-btn"
          onClick={() => {
            setTheme(switchToTheme);
            window.localStorage.setItem("theme", switchToTheme);
          }}
        >
          {switchToTheme + " Mode"}
        </button> */}
      <DarkModeSwitch
        checked={theme === Themes.DARK}
        onChange={() => {
          window.localStorage.setItem("theme", switchToTheme);
          setTheme(switchToTheme);
          document.body.className =
            "switching-theme " + (switchToTheme === Themes.DARK ? "dark" : "");
        }}
        size={35}
      />
    </div>
  );
};

export default ThemeSwitcher;
